  ActionsFromEmailResultsScreen (arg) {
    // console.log(arg);
    if (arg === "") {
      alert("Email-address is empty, please fill actual email")
    }
    else if (arg==="closeScreen") {
      // results@school108.om
      // console.log("closing the Screen")      
      // console.log(this.state.previousScreen);

      let newState = this.state.EmailResultsScreen;
      newState.componentClass = 'emailResultsScreen-move-2-down'
      this.setState({EmailResultsScreen: newState, currentScreen: this.state.previousScreen, previousScreen: "EmailResultsScreen"})

      let ActionsFromEmailResultsScreenNewState;

      switch (this.state.previousScreen) {
      case 'AttendanceScreen':
          ActionsFromEmailResultsScreenNewState = this.state.AttendanceScreen;
          ActionsFromEmailResultsScreenNewState.visible = true;
          ActionsFromEmailResultsScreenNewState.componentClass = 'attendanceScreen-move-2-up';
          this.setState({AttendanceScreen: ActionsFromEmailResultsScreenNewState, VerticalMenuContent: 'AttendanceScreen'});
        break;
        case 'QuestionsScreen':
          ActionsFromEmailResultsScreenNewState = this.state.QuestionsScreen;
          ActionsFromEmailResultsScreenNewState.visible = true;
          ActionsFromEmailResultsScreenNewState.componentClass = 'questionsScreen-move-2-left';
          this.setState({QuestionsScreen: ActionsFromEmailResultsScreenNewState, VerticalMenuContent: 'QuestionScreen'});
        break;
        case 'AnalysisScreen':
          ActionsFromEmailResultsScreenNewState = this.state.AnalysisScreen;
          ActionsFromEmailResultsScreenNewState.visible = true;
          ActionsFromEmailResultsScreenNewState.componentClass = 'questionsAnalysis-move-2-up';
          this.setState({AnalysisScreen: ActionsFromEmailResultsScreenNewState, VerticalMenuContent: 'AnalysisScreen'});
      break;
      default:
        console.log("at main App, ActionsFromEmailResultsScreen-function, switch-statement, wrong arg: " + this.state.previousScreen)
      }
 
    }
    else {
      let newState = this.state.EmailResultsScreen;
      newState.popupActive = true;
      newState.popupEmailAddress = arg;
      newState.popupStatus = 'sending';
      this.setState({EmailResultsScreen: newState})

      // temporary imitation of progress
      setTimeout(()=>{
        let newState = this.state.EmailResultsScreen;
        newState.popupStatus = 'sent';
        this.setState({EmailResultsScreen: newState})
      }, 3000)

    }
  }

